export default {
	data:function(){
		return {
			pageLoad:false, 
			pageData:{},
			notephone:"请输入手机号码",
			notepwd:"请输入密码",
			isWeixin:false,
			telephone:"",
			password:""
		}
	},
	onLoad:function(){
		this.isWeixin=this.app.isWeixin();
	},
	methods:{
		goWeixin:function(){
			var backurl="/pages/fenlei/index";
			this.app.goH5WeiXin(backurl);
			
		},
		goHome:function(){
			var that=this;
			that.app.goHome();
		},
		
		wxLogin: function(e) {
			var that=this;
			var user=e.detail.userInfo;
			uni.login({
				provider: 'weixin',
				success: function (res) {
					var logincode =res.code;
					console.log(logincode);
					uni.request({
						url:that.app.apiHost+"/?m=open_wxapp&a=Login&ajax=1",
						data:{
							code:logincode,
							nickname:user.nickName,
							user_head:user.avatarUrl,
							gender:user.gender
						},
						success:function(res){
							if(res.data.data.action=='login'){
								uni.showToast({
									title:"登录成功"
								})
								that.app.setAuthCode(res.data.data.authcode);
								that.app.setAuthCodeLong(res.data.data.authcodeLong);
								that.app.setOpenid(res.data.data.openid);
								setTimeout(function(){
									uni.navigateBack();
								},300);
							}else if(res.data.data.action=='openlogin'){
								 uni.navigateTo({
								 	url:"../openlogin/index?openToken="+res.data.data.openToken
								 })
							}	
							
							 
						},
						fail:function(e){
							console.log(e);
						}
					})
					
				} 
			});
		
		},
		formSubmit:function(e){
			var that=this;
			uni.request({
				url:that.app.apiHost+"?m=login&a=loginsave&ajax=1",
				method:"POST",
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				data:{
					telephone:that.telephone,
					password:that.password
				},
				success:function(res){
					var data=res.data;
					if(res.data.error){
						uni.showToast({
							"title":res.data.message
						})
					}else{
						that.app.setAuthCode(data.data.authcode);
						that.app.setAuthCodeLong(data.data.authcodeLong);
						uni.showToast({
							"title":res.data.message
						});
						setTimeout(function(){
							uni.navigateBack({
								delta:1,
							});
						},300)
					
						
					}
					 
				}
			})
			 
		}
	}
}
